<template>
  <div id="input-data">
    <span class="headline3">Исходные данные</span>

    <MathJax :latex="visualInputData" :block="true" />

    <div id="actions">
      <Btn label="Изменить" @click="showDialog = true" />
      <Btn label="Решить" filled @click="getAnswer()" />
    </div>
  </div>

  <div id="answer" v-if="isDone">
    <span class="headline5">
      Формула для вычисления коэффициентов
      <MathJax latex="\(\beta\)" />
    </span>
    <MathJax latex="\beta = (X^T X)^{-1} X^T \vec{y}" block />

    <span class="headline5">
      <b>Шаг 1: Найти <MathJax latex="\(X^T\)" /></b> (транспонированная матрица
      <MathJax latex="\(X\)" />):
    </span>
    <MathJax :latex="'X^T =' + matrixToHtml(XTranspose)" block />

    <span class="headline5">
      <b>Шаг 2: Найти <MathJax latex="\(X^T X\)" /></b> (перемножение
      транспонированной матрицы <MathJax latex="\(X\)" /> с самой матрицей
      <MathJax latex="\(X\)" />):
    </span>
    <MathJax :latex="'X^T X =' + matrixToHtml(XTX)" block />

    <span class="headline5">
      <b>Шаг 3: Найти <MathJax latex="\((X^T X)^{-1}\)" /></b> (обратная матрица
      для <MathJax latex="\(X^T X\)" />): </span
    ><br />
    Сначала вычислим определитель <MathJax latex="\(\text{det}(X^T X)\)" />, а
    затем используем метод Гаусса или формулу для обратной матрицы.<br /><br />

    <span class="headline5">
      <b>Шаг 4: Найти <MathJax latex="\(X^T \vec{y}\)" /></b>:
    </span>
    <MathJax :latex="'X^T \\vec{y} = ' + matrixToHtml(XTXInverse)" block />

    <span class="headline5">
      <b>Шаг 5: Найти <MathJax latex="\(\beta\)" /></b>, перемножив
      <MathJax latex="\((X^T X)^{-1}\)" /> на
      <MathJax latex="\(X^T \vec{y}\)" />:
    </span>
    <MathJax :latex="'\\beta = ' + matrixToHtml(beta)" block />

    <span class="headline5">Шаги нахождения коэффициента детерминации R:</span>
    <ol>
      <li>
        Рассчитываем прогнозные значения (<MathJax latex="\(X \beta\)" />)
      </li>
      <MathJax :latex="'X \\beta = ' + matrixToHtml(yHat)" block />

      <li>Рассчитываем среднее значение <MathJax latex="\(\vec{y}\)" />:</li>
      <MathJax :latex="`\\bar{y} = \\frac{1}{n} \\sum y_i = ${yMean}`" block />

      <li>Рассчитываем сумму квадратов отклонений:</li>
      <MathJax
        :latex="`\\text{SST} = \\sum_{i=1}^{n} (y_i - \\bar{y})^2 = ${SST}`"
        block
      />

      <li>Рассчитываем сумму квадратов регрессии:</li>
      <MathJax
        :latex="`\\text{SSR} = \\sum_{i=1}^{n} (\\hat{y}_i - \\bar{y})^2 = ${SSR}`"
        block
      />

      <li>Рассчитываем коэффициент:</li>
      <MathJax
        :latex="`R = \\frac{\\text{SSR}}{\\text{SST}} = ${SSR / SST}`"
        block
      />
    </ol>

    <span class="headline5">
      Шаги нахождения нормированных коэффициентов регрессии:
    </span>
    <ol>
      <li>
        Находим отклонения от случайного фактора по формуле
        <MathJax latex="\(e_i = y_i - \hat{y}_i\)" />:
      </li>
      <MathJax
        v-for="(val, i) in residuals"
        :latex="`e_${i + 1} = ${y[i][0]} - ${yHat[i][0].toFixed(
          4
        )} = ${val.toFixed(4)}`"
        block
      />

      <li>Рассчитываем стандартное отклонение остатков:</li>
      <MathJax
        :latex="'SE = \\sqrt{\\frac{\\text{SSR}}{n - p}} = ' + SE.toFixed(4)"
        block
      />
      Где:
      <ol style="list-style: circle">
        <li><MathJax latex="\( SE \)" /> — стандартное отклонение остатков,</li>
        <li>
          <MathJax latex="\( \text{SSR} \)" /> — сумма квадратов остатков,
        </li>
        <li>
          <MathJax latex="\( n \)" /> — общее количество наблюдаемых данных,
        </li>
        <li>
          <MathJax latex="\( p \)" /> — количество параметров модели (включая
          свободный член).
        </li>
      </ol>

      <li>Рассчитываем среднеквадратичное отклонение:</li>
      <MathJax
        :latex="`S_y = \\sqrt{\\frac{\\sum (y_i - \\bar{y})^2}{n}} = \\sqrt{\\frac{${SST.toFixed(
          4
        )}}{${y.length}}} = ${SEY.toFixed(4)}`"
        block
      />

      <li>
        Находим диагональные элементы матрицы
        <MathJax latex="\((X'X)^{-1}\)" />:
      </li>
      <MathJax
        v-for="(val, i) in diagonalElements"
        :latex="`b_{${i + 1}${i + 1}} = ${val.toFixed(4)}`"
        block
      />

      <li>
        Рассчитываем нормированные коэффициенты регрессии по формуле
        <MathJax latex="\(a_j = \frac{\beta_j S_{x_j}}{S_y}\)" />:
      </li>
      <MathJax
        v-for="(val, i) in betaNormalized"
        :latex="`a_${i} = ${val}`"
        block
      />
    </ol>
  </div>

  <Dialog
    v-model="showDialog"
    title="Ввод данных"
    description="Вводите значения матрицы через запятую, каждую строку разделяйте enter. Значения (y) можно ввести одной строкой разделяя запятой"
    permanent
  >
    <template v-slot:body>
      <textarea
        class="field"
        style="resize: vertical; height: 200px"
        v-model="x_dialog"
      ></textarea>
      <Field label="Вектор y" v-model="y_dialog" />
    </template>

    <template v-slot:actions>
      <Btn label="Закрыть" @click="showDialog = false" />
      <Btn label="Готово" filled @click="getInput()" />
    </template>
  </Dialog>
</template>

<script lang="ts">
// @ts-nocheck
import { defineComponent } from "vue";
import { MathJax } from "mathjax-vue3";
import Btn from "./components/Btn.vue";
import Field from "./components/Field.vue";
import Dialog from "./components/Dialog.vue";

export default defineComponent({
  name: "App",

  components: {
    Btn,
    Field,
    Dialog,
    MathJax,
  },

  data: () => ({
    isDone: false,
    showDialog: false,

    x_dialog: "",
    y_dialog: "",

    x_input: [
      [29, 7, 0.09, 2.8],
      [34, 9, 0.1, 4.1],
      [43, 26, 0.08, 4],
      [33, 24, 0.12, 5],
      [53, 13, 0.14, 2.7],
      [26, 12, 0.13, 3.4],
      [32, 23, 0.1, 4.8],
      [51, 8, 0.12, 2.9],
      [43, 22, 0.15, 3.7],
      [29, 9, 0.02, 3.5],
      [37, 12, 0.08, 5],
      [49, 5, 0.14, 4.1],
      [57, 11, 0.11, 3.6],
      [46, 15, 0.06, 4.7],
      [29, 21, 0.15, 2.8],
    ],
    y: [
      5.73, 7.85, 12.53, 12.28, 7.47, 5.23, 12.16, 6.86, 11.02, 7.77, 10.62,
      7.4, 10.55, 12.3, 7.83,
    ].map((val) => [val]),

    XTranspose: [],
    XTX: [],
    XTXInverse: [],
    beta: [],
    yHat: [],
    yMean: 0,
    SST: 0,
    SSR: 0,
    residuals: [],
    SE: 0,
    diagonalElements: [],
    SEY: 0,
    betaNormalized: [],
  }),

  computed: {
    x(): number[][] {
      return this.x_input.map((row) => [1, ...row]);
    },
    visualInputData(): string {
      return (
        "X = " +
        this.matrixToHtml(this.x_input) +
        ", \\quad \\vec{y} = " +
        this.matrixToHtml(this.y)
      );
    },
  },

  methods: {
    matrixToHtml(matrix: number[][], round = 2) {
      return (
        "\\begin{pmatrix} " +
        matrix
          .map((row) => {
            return row.map((col) => col.toFixed(round)).join(" & ");
          })
          .join("\\\\ ") +
        " \\end{pmatrix}"
      );
    },

    getInput() {
      this.x_input = this.x_dialog
        .trim()
        .split("\n")
        .map((row) => row.split(",").map((val) => parseFloat(val)));
      this.y = this.y_dialog
        .trim()
        .split(",")
        .map((val) => [parseFloat(val)]);
      this.showDialog = false;
    },

    getAnswer() {
      // Функция для транспонирования матрицы
      function transpose(matrix: number[][]) {
        return matrix[0].map((_, i) => matrix.map((row) => row[i]));
      }

      // Функция для умножения двух матриц
      function multiplyMatrices(matrix1: number[][], matrix2: number[][]) {
        return matrix1.map((row) =>
          transpose(matrix2).map((column) =>
            row.reduce((sum, val, i) => sum + val * column[i], 0)
          )
        );
      }

      // Функция для вычисления обратной матрицы
      function invertMatrix(matrix: number[][]) {
        let size = matrix.length;
        let identityMatrix = Array(size)
          .fill()
          .map(() => Array(size).fill());
        for (let i = 0; i < size; ++i) {
          for (let j = 0; j < size; ++j) {
            identityMatrix[i][j] = i === j ? 1 : 0;
          }
        }
        for (let i = 0; i < size; ++i) {
          let max = i;
          for (let j = i + 1; j < size; ++j) {
            if (Math.abs(matrix[j][i]) > Math.abs(matrix[max][i])) {
              max = j;
            }
          }
          [matrix[i], matrix[max]] = [matrix[max], matrix[i]];
          [identityMatrix[i], identityMatrix[max]] = [
            identityMatrix[max],
            identityMatrix[i],
          ];
          for (let j = 0; j < size; ++j) {
            if (j !== i) {
              let ratio = matrix[j][i] / matrix[i][i];
              for (let k = i + 1; k < size; ++k) {
                matrix[j][k] -= ratio * matrix[i][k];
              }
              for (let k = 0; k < size; ++k) {
                identityMatrix[j][k] -= ratio * identityMatrix[i][k];
              }
            }
          }
        }
        for (let i = 0; i < size; ++i) {
          let a = matrix[i][i];
          for (let j = 0; j < size; ++j) {
            identityMatrix[i][j] /= a;
          }
        }
        return identityMatrix;
      }

      // Функция для вычисления среднего значения
      function mean(arr: number[]) {
        const sum = arr.reduce((acc, val) => acc + val, 0);
        return sum / arr.length;
      }

      // Функция для вычисления стандартного отклонения
      function standardDeviation(arr: number[]) {
        const arrMean = mean(arr);
        const squareDiffs = arr.map((val) => Math.pow(val - arrMean, 2));
        const avgSquareDiff = mean(squareDiffs);
        return Math.sqrt(avgSquareDiff);
      }

      // Получение столбцов (независимых переменных) без первого столбца
      function getColumns(matrix: number[][], skipFirstColumn = true) {
        const numCols = matrix[0].length;
        const startCol = skipFirstColumn ? 1 : 0;
        const columns = [];

        for (let col = startCol; col < numCols; col++) {
          const column = matrix.map((row) => row[col]);
          columns.push(column);
        }

        return columns;
      }

      // Шаг 1: Транспонирование X
      this.XTranspose = transpose(this.x);

      // Шаг 2: X^T * X
      this.XTX = multiplyMatrices(this.XTranspose, this.x);

      // Шаг 3: (X^T * X)^-1
      this.XTXInverse = invertMatrix(this.XTX);

      // Шаг 4: X^T * y
      this.XTY = multiplyMatrices(this.XTranspose, this.y);

      // Шаг 5: Рассчитать beta
      this.beta = multiplyMatrices(this.XTXInverse, this.XTY);

      // Рассчитываем прогнозные значения y
      this.yHat = multiplyMatrices(this.x, this.beta);
      // Рассчитываем среднее значение y
      this.yMean = this.y.reduce((sum, val) => sum + val[0], 0) / this.y.length;
      // Рассчитываем сумму квадратов отклонений (SST)
      this.SST = this.y.reduce(
        (sum, val) => sum + (val[0] - this.yMean) ** 2,
        0
      );
      // Рассчитываем сумму квадратов регрессии (SSR)
      this.SSR = this.yHat.reduce(
        (sum, val) => sum + (val[0] - this.yMean) ** 2,
        0
      );

      // Остатки
      this.residuals = this.y.map((val, i) => val[0] - this.yHat[i][0]);
      // Стандартное отклонение остатков
      this.SE = Math.sqrt(this.SSR / (this.y.length - this.x[0].length));
      this.diagonalElements = invertMatrix(
        multiplyMatrices(transpose(this.x), this.x)
      ).map((row, i) => row[i]);

      // Стандартные ошибки коэффициентов
      let SEBeta = this.diagonalElements.map((val) =>
        Math.sqrt(this.SE ** 2 * val)
      );
      this.SEY = Math.sqrt(
        this.y.reduce((sum, val) => sum + (val[0] - this.yMean) ** 2, 0) /
          this.y.length
      );

      // Вычисление стандартных отклонений для независимых переменных
      const independentVariables = getColumns(this.x);
      const SEIndependents = independentVariables.map((column) =>
        standardDeviation(column)
      );
      // Нормированные коэффициенты регрессии
      this.betaNormalized = SEIndependents.map(
        (val, i) => (val * this.beta[i + 1]) / this.SEY
      );

      this.isDone = true;
    },
  },
});
</script>

