<template>
  <label class="field" :class="{ empty: !modelValue, error, focussed }">
    <span v-if="label" class="label" v-text="label" />
    <input
      :type="type"
      :name="name"
      :value="modelValue"
      :autocomplete="autocomplete"
      @input="inputText"
      @focus="focussed = true"
      @blur="focussed = false"
    />
    <span
      class="icon material-icons-round"
      @click="$emit('update:modelValue', '')"
      v-text="error ? '&#xe001;' : '&#xe888;'"
    />
  </label>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "Text Field",

  props: {
    type: { type: String, default: "text" },
    name: { type: String, default: "" },
    label: { type: String, default: "" },
    error: { type: Boolean, default: false },
    modelValue: { type: String, default: "" },
    autocomplete: { type: String, default: "off" },
  },

  emits: ["update:modelValue"],

  data: () => ({
    focussed: false,
  }),

  methods: {
    inputText(e: Event) {
      const el = e.target as HTMLInputElement;
      if (!el) return;
      this.$emit("update:modelValue", el.value);
    },
  },
});
</script>

<style lang="scss">
.field {
  gap: 16px;
  width: calc(100% - 34px);
  cursor: text;
  margin: 8px 0;
  border: 1px solid;
  display: flex;
  padding: 8px 16px;
  position: relative;
  transition: border-color 250ms ease-in-out;
  align-items: center;
  border-radius: 4px;
  flex-direction: row;

  color: $md-sys-color-on-surface-variant-light;
  border-color: $md-sys-color-outline-light;

  &:hover {
    border-color: $md-sys-color-on-surface-light;
  }

  &.focussed {
    border-color: $md-sys-color-primary-light;
  }

  &.error {
    border-color: $md-sys-color-error-light;
  }

  input,
  textarea {
    width: 100%;
    border: none;
    outline: none;
    background: transparent;
    border-radius: 4px;
    @include body-large;

    color: $md-sys-color-on-surface-light;
  }

  .label {
    @include body-large;

    top: 50%;
    left: 14px;
    padding: 0 4px;
    position: absolute;
    transform: translateY(-50%);
    transition: top 250ms ease-in-out, font 250ms ease-in-out,
      color 250ms ease-in-out;
    user-select: none;

    color: $md-sys-color-on-surface-light;
    background: $md-sys-color-surface-light;
  }

  &.focussed .label,
  &:not(.empty) .label {
    @include body-small;
    top: -0px;
  }

  &.focussed .label {
    color: $md-sys-color-primary-light;
  }

  &.error .icon,
  &.error .label {
    color: $md-sys-color-error-light;
  }

  .icon {
    cursor: pointer;
    transition: color 250ms ease-in-out;
    -webkit-tap-highlight-color: transparent;
  }
  .icon:active {
    color: $md-sys-color-on-surface-light;
  }
}
</style>
